@import '../variables';

.mini-cart {
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 101;
    color: $dark;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 100%;
    height: 100vh;
    transition: ease-out 0.3s;

    .mini-cart-container {
        position: absolute;
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 80%;
        background: $light;
        border-top: 1px solid $dark;
        padding: 16px;
        transition: ease-out 0.3s;
        overflow: auto;
        overscroll-behavior: contain;

        .title {
            display: flex;
            justify-content: space-between;
            width: 100%;
            margin-bottom: 1.6rem;
        }

        p {
            font-size: 1.6rem;
            margin-right: 16px;
        }
    }
}
